import Vue from "vue";
import Vuex from "vuex";
import axios from "@/utils/request";
import { Message } from "element-ui";

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        token: localStorage.token,
        username: localStorage.username,
        search: false,
        articles: [],
        count: 0,
    },
    mutations: {
        setToken(state, token) {
            state.token = token;
            localStorage.token = token;
        },
        setUsername(state, username) {
            state.username = username;
            localStorage.username = username;
        },
        deleteToken(state) {
            state.token = "";
            localStorage.removeItem("token");
            localStorage.removeItem("username");
        },
        setSearch(state) {
            state.search = !state.search;
        },
        setArticle(state, list) {
            state.articles = list;
        },
        setCount(state, count) {
            state.count = count;
        },
    },
    actions: {
        async searchKeyTitle({ commit }, title) {
            let data = await axios.get(`/search/${title}`);
            let p = new Promise((resolve, reject) => {
                resolve(data);
            });
            return p;
        },
        async searchTags({ commit }, tag) {
            let data = await axios.get(`/search/tag/${tag}`);
            let p = new Promise((resolve, reject) => {
                resolve(data);
            });
            return p;
        },
        async login({ commit }, { username, password }) {
            let data = await axios.post("/login", { username, password });
            let p = new Promise((resolve, reject) => {
                resolve(data);
            });
            return p;
        },
        async register({ commit }, { username, password }) {
            try {
                let data = await axios.post("/users", { username, password });
                let token = data.data.token;
                commit("setToken", token);
            } catch (e) {
                Message.error("register failed~~");
            }
        },
        async logout({ commit }) {
            let p = new Promise((resolve, reject) => {
                commit("deleteToken");
                resolve("success");
            });
        },
    },
});

export default store;
