    margin-bottom: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    align-items: center;
    font-family: "Exo", sans-serif;
    color: var(--text-color-darker);
}
.me {
    font-size: 35px;
}
.atwood {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
}
.flex {
    display: flex;
    position: absolute;
    top: 32%;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
}
.mark {
    border-right: 2px solid #fff;
    animation: blink 0.6s step-end infinite;
    margin-left: 4px;
}
@keyframes blink {
    from,
    to {
        border-color: transparent;
    }
    50% {
        border-color: #fff;
    }
}
</style>
