<template>
    <nav class="navigation" :class="{ 'box-shadow': $route.path != '/' }">
        <input type="checkbox" id="check-box" ref="checkbox" />
        <div class="nav-list" ref="navlist">
            <a
                :target="nav.target ? '_blank' : '_self'"
                @click="
                    navStatus();
                    uncheck();
                "
                :href="nav.path"
                v-for="(nav, index) in navList"
                :key="index"
            >
                {{ nav.title }}
            </a>
        </div>
        <div class="user" v-if="$store.state.username">
            <span class="username">{{ $store.state.username }}</span>
            <span class="logout"
                ><el-button @click="open" type="text">[ 退出 ]</el-button></span
            >
        </div>
        <div
            class="search-show"
            v-if="$route.fullPath == '/' && searchFlag === false"
        >
            <i @click="searchShow"></i>
        </div>
        <div class="burger" @click="navStatus">
            <label class="check-box" for="check-box">
                <div class="burger-line1"></div>
                <div class="burger-line2"></div>
                <div class="burger-line3"></div>
                <a href="" target="_blank"></a>
            </label>
        </div>
    </nav>
</template>

<script>
import { mapActions } from "vuex";

export default {
    data() {
        return {
            navList: [
                {
                    title: "首頁",
                    path: "/",
                },
                {
                    title: "素材工具",
                    path: "/#/tool",
                },
                {
                    title: "關於博主",
                    path: "/#/about",
                },
                {
                    title: "Github",
                    path: "https://github.com/kkluffy",
                    target: "https://github.com/kkluffy",
                },
                {
                    title: "登入/註冊",
                    path: "/#/login",
                },
            ],
            searchFlag: false,
        };
    },
    methods: {
        ...mapActions(["logout"]),
        searchShow() {
            this.$store.commit("setSearch");
        },
        navStatus() {
            if (!this.$refs.navlist.className.includes("show")) {
                this.$refs.navlist.classList.add("show");
                this.searchFlag = true;
            } else {
                this.$refs.navlist.classList.remove("show");
                this.searchFlag = false;
            }
        },
        uncheck() {
            this.$refs.checkbox.checked = false;
        },
        open() {
            this.$confirm("确定要退出吗？", "提示", {
                confirmButtonText: "確定",
                cancelButtonText: "取消",
                type: "warning",
            })
                .then(() => {
                    this.logout();
                    this.$message.success("退出成功~");
                })
                .then(() => {
                    setTimeout(() => {
                        location.reload();
                    }, 2000);
                })
                .catch(() => {
                    this.$message.info("已取消退出");
                });
        },
    },
};
</script>

<style>
#check-box {
    display: none;
}
.check-box {
    display: block;
    width: 100%;
    height: 100%;
}
.navigation {
    width: 100vw;
    display: flex;
    justify-content: right;
    align-items: center;
    height: 40px;
    position: absolute;
    background-color: transparent;
    z-index: 111;
}
.user {
    margin-right: 10px;
}
.user > span {
    margin-top: -3px;
    display: inline-block;
    line-height: 40px;
}
.user > .logout > button {
    color: #000;
    font-weight: 400;
}
.box-shadow {
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.nav-list {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 0;
    overflow: hidden;
    background: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: height linear 0.2s;
}
.show {
    height: 100vh;
}
/* #check-box:checked + .nav-list {
    height: 100vh;
} */
.nav-list a {
    margin: 20px 0;
    color: #888;
    transition: color linear 0.2s;
    cursor: pointer;
}
.nav-list a:hover {
    color: var(--text-color-darker);
}
.burger {
    display: block;
    width: 20px;
    height: 6px;
    position: relative;
    margin-right: 15px;
    cursor: pointer;
    z-index: 100;
}
.burger-line1,
.burger-line2,
.burger-line3 {
    width: 20px;
    height: 2px;
    transition: 0.4s ease;
    background-color: var(--text-color-darker);
}
.burger-line1 {
    position: absolute;
    top: 6px;
}
.burger-line3 {
    position: absolute;
    top: -6px;
}
#check-box:checked ~ .burger .burger-line1,
#check-box:checked ~ .burger .burger-line2,
#check-box:checked ~ .burger .burger-line3 {
    transition: 0.4s ease;
}
#check-box:checked ~ .burger .burger-line1 {
    transform: rotate(45deg) translate(1px, -7px);
}
#check-box:checked ~ .burger .burger-line2 {
    transform: translateX(5px);
    opacity: 0;
}
#check-box:checked ~ .burger .burger-line3 {
    transform: rotate(-45deg) translate(-1px, 10px);
}

.search-show i {
    background-image: url("/imgs/icon/search.png");
    transform: scale(1.3);
    cursor: pointer;
    display: inline-block;
    width: 15px;
    height: 15px;
    background-repeat: no-repeat;
    background-size: 100%;
    margin-right: 10px;
}
</style>
