<template>
    <div class="login">
        <div class="login-container">
            <ul>
                <li
                    v-for="(log, index) in logForm"
                    :key="index"
                    @click="fnIndex(index)"
                    :class="{ active: index === logCur }"
                >
                    {{ log.title }}
                </li>
            </ul>
            <div class="log" v-if="logCur == 0">
                <div class="form-control">
                    <input
                        type="text"
                        id="username"
                        v-model="loginData.username"
                        @blur="usernameCheck2"
                        required
                    />
                    <label for="username">
                        <span style="transition-delay: 0ms">u</span
                        ><span style="transition-delay: 50ms">s</span
                        ><span style="transition-delay: 100ms">e</span
                        ><span style="transition-delay: 150ms">r</span
                        ><span style="transition-delay: 200ms">n</span
                        ><span style="transition-delay: 250ms">a</span
                        ><span style="transition-delay: 300ms">m</span
                        ><span style="transition-delay: 350ms">e</span>
                    </label>
                    <small v-if="errorMsg2[0].status">{{
                        errorMsg2[0].username
                    }}</small>
                </div>
                <div class="form-control">
                    <input
                        type="password"
                        id="password"
                        v-model="loginData.password"
                        @blur="passwordCheck3"
                        required
                    />
                    <label for="password">
                        <span style="transition-delay: 0ms">p</span
                        ><span style="transition-delay: 50ms">a</span
                        ><span style="transition-delay: 100ms">s</span
                        ><span style="transition-delay: 150ms">s</span
                        ><span style="transition-delay: 200ms">w</span
                        ><span style="transition-delay: 250ms">o</span
                        ><span style="transition-delay: 300ms">r</span
                        ><span style="transition-delay: 350ms">d</span>
                    </label>
                    <small v-if="errorMsg2[1].status">{{
                        errorMsg2[1].password
                    }}</small>
                </div>
                <button @click="logIn">提交</button>
            </div>
            <div class="register" v-if="logCur == 1">
                <div class="form-control">
                    <input
                        type="text"
                        id="username"
                        v-model="loginData.username"
                        @blur="usernameCheck"
                        required
                    />
                    <label for="username">
                        <span style="transition-delay: 0ms">u</span
                        ><span style="transition-delay: 50ms">s</span
                        ><span style="transition-delay: 100ms">e</span
                        ><span style="transition-delay: 150ms">r</span
                        ><span style="transition-delay: 200ms">n</span
                        ><span style="transition-delay: 250ms">a</span
                        ><span style="transition-delay: 300ms">m</span
                        ><span style="transition-delay: 350ms">e</span>
                    </label>
                    <small v-if="errorMsg[0].status">{{
                        errorMsg[0].username
                    }}</small>
                </div>
                <div class="form-control">
                    <input
                        type="password"
                        id="password"
                        v-model="loginData.password"
                        @blur="passwordCheck"
                        required
                    />
                    <label for="password">
                        <span style="transition-delay: 0ms">p</span
                        ><span style="transition-delay: 50ms">a</span
                        ><span style="transition-delay: 100ms">s</span
                        ><span style="transition-delay: 150ms">s</span
                        ><span style="transition-delay: 200ms">w</span
                        ><span style="transition-delay: 250ms">o</span
                        ><span style="transition-delay: 300ms">r</span
                        ><span style="transition-delay: 350ms">d</span>
                    </label>
                    <small v-if="errorMsg[1].status">{{
                        errorMsg[1].password
                    }}</small>
                </div>
                <div class="form-control">
                    <input
                        type="password"
                        id="password2"
                        v-model="loginData.password2"
                        @blur="passwordCheck2"
                        required
                    />
                    <label for="password2">
                        <span style="transition-delay: 0ms">c</span
                        ><span style="transition-delay: 50ms">o</span
                        ><span style="transition-delay: 100ms">m</span
                        ><span style="transition-delay: 150ms">f</span
                        ><span style="transition-delay: 200ms">i</span
                        ><span style="transition-delay: 250ms">r</span
                        ><span style="transition-delay: 300ms">m</span>
                    </label>
                    <small v-if="errorMsg[1].status2">{{
                        errorMsg[1].password2
                    }}</small>
                </div>
                <button @click="registerFn">提交</button>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions, mapMutations } from "vuex";
export default {
    data() {
        return {
            loginData: {
                username: "",
                password: "",
                password2: "",
            },
            errorMsg: [
                {
                    username: "用户名长度不能小於5位",
                    status: false,
                },
                {
                    password: "密碼長度不能小於5位",
                    password2: "兩次密碼不一致",
                    status: false,
                    status2: false,
                },
            ],
            errorMsg2: [
                {
                    username: "用户名长度不能小於5位",
                    status: false,
                },
                {
                    password: "密碼長度不能小於5位",
                    status: false,
                },
            ],
            logCur: 0,
            logForm: [
                {
                    title: "登錄",
                },
                {
                    title: "註冊",
                },
            ],
        };
    },
    methods: {
        ...mapActions(["login", "register"]),
        ...mapMutations(["setToken", "setUsername"]),
        async logIn() {
            if (
                this.loginData.username.length >= 5 &&
                this.loginData.password.length >= 5
            ) {
                let data = {
                    username: this.loginData.username,
                    password: this.loginData.password,
                };
                let res = await this.login(data);
                let { status, message, username, token } = res.data;
                if (status >= 400) {
                    this.$message.error(message);
                    return;
                } else {
                    this.setToken(token);
                    this.setUsername(username);
                    this.$router.push("/");
                    this.$message.success("Login success~~");
                }
            } else {
                this.$message.error("賬號或密碼有誤，請重新輸入~");
            }
        },
        async registerFn() {
            if (
                this.loginData.username.length >= 5 &&
                this.loginData.password.length >= 5 &&
                this.loginData.password2 === this.loginData.password
            ) {
                let data = {
                    username: this.loginData.username,
                    password: this.loginData.password,
                };
                await this.register(data);
                this.$store.commit("setUsername", this.loginData.username);
                this.$router.push("/");
                this.$message.success("註冊成功~");
            } else {
                this.$message.error("賬號或密碼有誤，請重新輸入~");
            }
        },
        fnIndex(index) {
            this.logCur = index;
            if (this.loginData.username || this.loginData.password) {
                this.loginData.username = "";
                this.loginData.password = "";
            }
        },
        usernameCheck() {
            if (this.loginData.username.length < 5) {
                this.errorMsg[0].status = true;
            } else {
                this.errorMsg[0].status = false;
            }
        },
        usernameCheck2() {
            if (this.loginData.username.length < 5) {
                this.errorMsg2[0].status = true;
            } else {
                this.errorMsg2[0].status = false;
            }
        },
        passwordCheck() {
            if (this.loginData.password.length < 5) {
                this.errorMsg[1].status = true;
            } else {
                this.errorMsg[1].status = false;
            }
        },
        passwordCheck3() {
            if (this.loginData.password.length < 5) {
                this.errorMsg2[1].status = true;
            } else {
                this.errorMsg2[1].status = false;
            }
        },
        passwordCheck2() {
            if (this.loginData.password != this.loginData.password2) {
                this.errorMsg[1].status2 = true;
            } else {
                this.errorMsg[1].status2 = false;
            }
        },
    },
};
</script>

