<template>
    <div class="search" :class="{ height100: $store.state.search }">
        <div class="close">
            <div class="close-btn" @click="searchShow">
                <span class="line line1"></span>
                <span class="line line2"></span>
            </div>
        </div>
        <div class="search-input common-style">
            <input
                type="text"
                placeholder="請輸入文章題目關鍵字"
                v-model="keyTitle"
            />
            <input
                type="text"
                id="search-icon"
                readonly="readonly"
                @click="searchTitle"
            />
        </div>
        <div class="category-tag common-style">
            <i></i><span>分類</span>
            <div class="tags">
                <span
                    @click="searchTag(tag.title)"
                    v-for="(tag, index) in cate"
                    :key="index"
                    >{{ tag.title }}</span
                >
            </div>
        </div>
        <div class="cloud-tag common-style">
            <i></i><span>標籤雲</span>
            <div class="tags">
                <span
                    @click="searchTag(tag.title)"
                    v-for="(tag, index) in tagsCloud"
                    :key="index"
                    >{{ tag.title }}</span
                >
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        articles: {
            type: Array,
        },
    },
    data() {
        return {
            keyTitle: "",
            cate: [
                {
                    title: "前端",
                },
                {
                    title: "后端",
                },
            ],
            tagsCloud: [
                {
                    title: "JavaScript",
                },
                {
                    title: "css",
                },
            ],
        };
    },
    methods: {
        async searchTag(title) {
            let res = await this.$store.dispatch("searchTags", title);
            this.$store.commit("setArticle", res.data[0]);
            this.$store.commit("setCount", res.data[1][0].c);
            this.$emit("change", this.$store.state.articles);
            this.$store.commit("setSearch");
        },
        searchShow() {
            this.$store.commit("setSearch");
        },
        async searchTitle() {
            if (this.keyTitle === "") {
                this.$message.warning("參數不能為空");
                return;
            }
            let res = await this.$store.dispatch(
                "searchKeyTitle",
                this.keyTitle
            );
            if (res.data[0].length === 0) {
                this.$message.warning("沒有搜尋到對應文章呦~~");
                return;
            }
            this.$store.commit("setArticle", res.data[0]);
            this.$store.commit("setCount", res.data[1][0].c);
            this.$emit("change", this.$store.state.articles);
            this.$store.commit("setSearch");
        },
        // async searchTitle() {
        //     if (this.keyTitle === "") {
        //         this.$message.warning("參數不能為空");
        //         return;
        //     }
        //     let res = await searchKeyTitle(this.keyTitle);
        //     this.$store.commit("setArticle", res.data[0]);
        //     this.$store.commit("setCount", res.data[1][0].c);
        //     this.$store.commit("setSearch");
        // },
    },
};
</script>

